import React from "react";


import ButtonIcon from "components/ButtonIcon/ButtonIcon";
import Page from "components/Page/Page";
import PageTitle from "components/PageTitle/PageTitle";
import Container from "components/Container/Container";

import styles from "./Aluno.module.scss";

const Volunteer = () => {
    return (
        <Page>
            <PageTitle
                title="Venha estudar conosco!"
                icon="fas fa-user-graduate"
                height="250px"
            />

            <div className={styles.container}>
                <Container>
                    <div className={styles.title}>
                        <h1 className="mt-5">Quer estudar no projeto ALES?</h1>
                    </div>

                    <div className={styles.description}>
                        <p>
                        Deseja participar no prximo semestre? Preencha o formulrio de interesse disponvel no final desta pgina.
                        </p>

                        <p>
                            <strong><big>Por que estudar no ALES?</big></strong>
                            <p></p>
                            <ul>
                                <li>Aprender contedos diferentes dos oferecidos nas escolas;</li>
                                <li>Descobrir novas oportunidades;</li>
                                <li>Entrar em contato com estudantes universitrios;</li>
                                <li>Ampliar a viso de mundo atravs de um ensino mais prtico;</li>
                            </ul>
                        </p>

                        <p>
                            <strong><big>Quais so as aulas?</big></strong>
                            <p></p>
                            <ul>
                                <li><strong>CINCIAS:</strong> Buscamos atiar a curiosidade natural que todos ns temos para entender o funcionamento da natureza e para enxergar como a biologia, a fsica e a qumica esto presentes no nosso dia-a-dia.</li>
                                <li><strong>DESENHO:</strong> Queremos que voc possa colocar as imagens de sua cabea no papel! Para tornar isso possvel vamos estudar fundamentos importantes como desenho de observao, corpo humano, luz e sombra, entre muitos outros. Venha com a gente nessa jornada!</li>
                                <li><strong>HUMANIDADES:</strong> Procuramos quebrar as barreiras entre filosofia, literatura, poltica, arte, mitologia e outras reas do conhecimento humanstico para entender a Histria atravs de uma outra perspectiva.</li>
                                <li><strong>INGLS:</strong> Trabalhamos todas as faces da aprendizagem de uma lngua estrangeira para iniciantes, desde a fala e a escuta at a gramtica e a escrita, incentivando a busca pelo conhecimento da lngua inglesa em prticas prazerosas, como na msica ou no cinema.</li>
                                <li><strong>MATEMTICA:</strong> Buscamos ir alm das contas e desenvolver o raciocnio lgico, a leitura e a interpretao de texto. Mostrando como a matemtica pode ser divertida e est presente em todo lugar no nosso dia-a-dia!</li>
                                <li><strong>PROGRAMAO:</strong> Nosso objetivo em programao  estimular os alunos a enfrentar novos desafios numa matria que nem sempre  oferecida no ensino comum. Onde podemos desenvolver jogos e aplicaes de uma forma divertida e prtica, abordando desde os conceitos mais bsicos da programao.</li>
                            </ul>
                        </p>

                        <p>
                        Venha desbravar diferentes aprendizados com a gente! Voc pode se inscrever em quantas disciplinas quiser! Clique no link abaixo e preencha o formulrio. Assim que as nossas inscries abrirem, entraremos em contato com voc/seu responsvel!
                        </p>

                        <div className={styles.buttons}>
                            <a
                                className={styles.button}
                                href="https://docs.google.com/forms/d/e/1FAIpQLSfnJZ9zMi_gICFFW8V2Hqvl9tjSafJr_JyqODtxzD_tr8mqJQ/viewform?usp=sf_link"
                                target="_blank"
                                rel="noopener noreferrer"
                            >
                                <ButtonIcon text="Quero ser avisado(a) quando as inscries estiverem abertas!" kind="yellow" icon="far fa-file-alt" />
                            </a>
                        </div>
                    </div>
                </Container>
            </div>
        </Page>
    );
};

export default Volunteer;