import React from "react";

import Container from "components/Container/Container";

import Logo from "assets/logos/logo.svg";
import styles from "./WhatIs.module.scss";

const WhatIs = () => {
  return (
    <Container>
      <div className={styles.whatisContainer}>
        <div className={styles.whatisTitle}>
          <h2>O que  o ALES?</h2>
        </div>
        <div className={styles.whatisDescription}>
          <p>
          A curiosidade e a vontade de aprender sao essenciais na vida dos jovens, mas muitas vezes estas sao suprimidas pela falta de acesso as informacoes e sistema de ensino vigente em nosso pais. Reconhecemos que a culpa disso no e das escolas e desta percepcao nasceu o ALES, um projeto que tem como objetivo trazer contedos alm da escola, complementando o desenvolvimento escolar e pessoal de estudantes do ensino mdio, em especial da rede pblica.
Nosso projeto no e um substituto da escola, e sim uma ferramenta para expandir horizontes e inspirar estudantes a irem alm na busca por novos conhecimentos.

          </p>
          
        </div>
      </div>
    </Container>
  );
};

export default WhatIs;
